import { expect } from 'chai';
import { PRINTABLE_ASCII } from '../const';
import v from '../voca';

describe('latinise', function() {

  it('should latinise a string', function() {
    expect(v.latinise('')).to.be.equal('');
    expect(v.latinise('moldova')).to.be.equal('moldova');
    expect(v.latinise('cafe\u0301')).to.be.equal('cafe');
    expect(v.latinise('ma\xF1ana')).to.be.equal('manana');
    expect(v.latinise('man\u0303ana')).to.be.equal('manana');
    expect(v.latinise('foo\u0303\u035C\u035D\u035Ebar')).to.be.equal('foobar');
    expect(v.latinise('cafe\u0301')).to.be.equal('cafe');
    expect(v.latinise('colec칞칚o c칩pias cr칤ticos 칠 t칚o')).to.be.equal('coleccao copias criticos e tao');
    expect(v.latinise('knji쬰vno코캖u 캜uvanje')).to.be.equal('knjizevnoscu cuvanje');
    expect(v.latinise('anglikon콥 코iurk코캜ios ura코in캥ti')).to.be.equal('anglikonu siurkscios uzrasineti');
    expect(v.latinise('Schu f칲r Pfarrerst칬chter')).to.be.equal('Schus fur Pfarrerstochter');
    expect(v.latinise('public칩 칠xito naci칩 Mar칤a')).to.be.equal('publico exito nacio Maria');
    expect(v.latinise('Charlotte Bront칢')).to.be.equal('Charlotte Bronte');
    expect(v.latinise('vec캐k캐 no m캐s캐m Bront캡')).to.be.equal('vecaka no masam Bronte');
    expect(v.latinise('료rlotta Brontenin 칬z칲')).to.be.equal('Sarlotta Brontenin ozu');
    expect(v.latinise('Wkr칩tce po 콑lubie pisarka zasz켹a w ci캔콮캧')).to.be.equal('Wkrotce po slubie pisarka zaszla w ciaze');
    expect(v.latinise("D칟s l'enfance, Charlotte, comme Emily et probablement plus fortement Branwell, est influenc칠e par certaines sources d'inspiration"))
      .to.be.equal("Des l'enfance, Charlotte, comme Emily et probablement plus fortement Branwell, est influencee par certaines sources d'inspiration");
    expect(v.latinise('Exist캒 peste 13.800 de localit캒탵i 칥n Rom칙nia'))
      .to.be.equal('Exista peste 13.800 de localitati in Romania');
    expect(v.latinise('ao칱t d칠cembre')).to.be.equal('aout decembre');
    expect(v.latinise('햒햨햟혱햫햟 쮏혝햟혣쮏쒫썛쫧  혰쒫얧왏쫧쫧-혜혠혰햢햫혰햧 혢햟혜혝햦햫혰 핷쒬쮏햦'))
      .to.be.equal('Ukrayina roztashovana v pivdenno-shidnij chastini Yevropi');
    expect(v.latinise('\t\n')).to.be.equal('\t\n');
    expect(v.latinise('\u2047')).to.be.equal('\u2047');
    expect(v.latinise(PRINTABLE_ASCII)).to.be.equal(PRINTABLE_ASCII);
  });

  it('should latinise a string representation of an object', function() {
    expect(v.latinise(['Mar칤a'])).to.be.equal('Maria');
    expect(v.latinise({
      toString: function() {
        return 'sac칩';
      }
    })).to.be.equal('saco');
  });

  it('should not modify numbers', function() {
    expect(v.latinise(100)).to.be.equal('100');
    expect(v.latinise(812)).to.be.equal('812');
  });

  it('should return an empty string for null or undefined', function() {
    expect(v.latinise()).to.be.equal('');
    expect(v.latinise(undefined)).to.be.equal('');
    expect(v.latinise(null)).to.be.equal('');
  });

});